<template>
    <div>
        <p style="text-align:left; margin:0;">
          <el-button type="primary" size="mini" @click="printBtn()"></el-button>
        </p>
        <div id="print">
              <h3></h3>
               <!--  <div class="biaotou">
                    <p><span>{{medicalText.hospitalName}}</span></p>
                    <p><span>{{medicalText.hospitalGrade}}</span></p>
                </div> -->
                <table class="ta" border="1" cellspacing="0" cellpadding="1" style="margin: auto">
                    <!--<tr>
                        <td></td>
                        <td colspan="2"></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td colspan="2"></td>
                    </tr>-->
                    <tr align='center'>
                        <td></td>
                        <td>{{issueData.xm}}</td>
                        <td></td>
                        <td>{{issueData.xb==1?"":""}}</td>
                         <td></td>
                        <td>{{issueData.csrq}}</td>
                       <!--  <td></td>
                        <td>{{issueData.nl}}</td> -->
                         <td></td>
                        <td>{{issueData.mz=='01'?"":""}}</td>              
                    </tr>
                    <tr align='center'>
                      <td></td>
                        <td>{{issueData.dh}}</td>
                       <td></td>
                        <td>{{issueData.sfzh}}</td>
                        <td></td>
                        <td>{{issueData.zydm}}</td> 
                          <td></td>
                        <td>{{issueData.hyzk}}</td>                       
                    </tr>
                    <tr align='center'>
                        <td></td>
                        <td>{{issueData.bah}}</td>
                         <td></td>
                        <td>{{issueData.ylfkfs}}</td>
                        <td></td>
                        <td>{{issueData.zfy}}</td>
                         <td></td>
                        <td>{{issueData.qzzfje}}</td>
                    </tr>
                    <tr align='center'>
                        <!-- <td></td>
                        <td colspan="3">{{issueData.zyzd}}</td> -->
                        <td></td>
                        <td>{{issueData.rytj}}</td>
                         <td></td>
                        <td>{{issueData.ryrq}}</td>
                        <td></td>
                        <td>{{issueData.cyrq}}</td>   
                        <!-- <td></td>
                        <td>{{issueData.cybf}}</td> -->
                       <!--  <td></td>
                        <td>{{issueData.zfy}}</td> -->
                        <td></td>
                        <td>{{issueData.zycs}}</td>
                    </tr>
                    <tr align='center'>
                        <td></td>
                        <td>{{issueData.mjzzd}}</td>
                         <td></td>
                        <td>{{issueData.jbdm}}</td>           
                        <td></td>
                        <td>{{issueData.lyfs}}</td>
                        <td></td>
                        <td>{{issueData.zyts}}</td>
                    </tr>
                    <tr align='center'>
                        <td></td>
                        <td>{{issueData.cyszyzd}}</td>
                         <td></td>
                        <td colspan="2">{{issueData.cysqtzd}}</td>           
                        <td></td>
                        <td colspan="2">{{issueData.rybq}}</td>
                        
                    </tr>
                     <tr align='center'>
                        <td></td>
                        <td>{{issueData.zhylfwl}}</td>
                         <td></td>
                        <td>{{issueData.ybylfwf}}</td>           
                        <td></td>
                        <td>{{issueData.ybzlczfy}}</td>
                        <td></td>
                        <td>{{issueData.hlf}}</td>
                    </tr>
                     <tr align='center'>           
                        <td colspan="3"></td>
                        <td>{{issueData.fsszlxmf}}</td>
                        <td colspan="3"></td>
                        <td>{{issueData.sszlf}}</td>
                    </tr>
                     <tr align='center'>
                        <td></td>
                        <td>{{issueData.xyf}}</td>
                         <td></td>
                        <td>{{issueData.kjywfy}}</td>           
                        <td></td>
                        <td>{{issueData.zcyfy}}</td>
                        <td></td>
                        <td>{{issueData.zcyf}}</td>
                    </tr>
                    
                   <!--  <tr>
                        <td colspan="8">&nbsp;&nbsp;&nbsp;&nbsp;</td>
                    </tr>
                    <tr align='center'>
                        <td></td>
                        <td></td>
                        <td colspan="2"></td>
                        <td colspan="2"></td>
                        <td></td>
                        <td></td>
                    </tr>
                    <tr align='center' v-for="(item,index) in issueData" :key="index">
                        <td style="width:50px;">{{index+1}}</td>
                        <td>{{item.itemName}}</td>
                        <td colspan="2">{{item.message}}</td>
                        <td colspan="2">{{item.type}}</td>
                        <td>{{item.amount}}</td>
                        <td>{{item.sum}}</td>
                    </tr> -->
                </table>
                <br>
                <br>
        </div>
        <!-- <button  @click="printBtn()"> </button> -->
    </div>
    
</template>



<script>
import axios from "axios";
export default {
  name: "Medical",
  props: ["medicalText"],
  data() {
    return {
      issueData: [],
      day: ""
    };
  },
  methods: {
    //
    getMedical() {
      axios({
        method: "post",
        url: axios.PARK_API + "/guar/biz/health/queryn041Info",
        params: {
          //   orderId: "74ddedfc-fcf0-40e8-bfc6-3653e0fd9d13"
          bah: this.medicalText.bah,
          hosgrade: localStorage.getItem("hosgrade")
        }
        /*  headers: {
          "Content-Type": "application/json;charset=UTF-8"
        } */
      })
        .then(res => {
          if (res.data.code == 0) {
            this.issueData = res.data.data;
            console.log(this.issueData.lyfs);
            //
            switch (this.issueData.lyfs) {
              case "1":
                this.issueData.lyfs = "";
                break;
              case "2":
                this.issueData.lyfs = "";
                break;
              case "3":
                this.issueData.lyfs = "/";
                break;
              case "4":
                this.issueData.lyfs = "";
                break;
              case "5":
                this.issueData.lyfs = "";
                break;
              default:
                this.issueData.lyfs = "";
                break;
            }
            //
            switch (this.issueData.rytj) {
              case "1":
                this.issueData.rytj = "";
                break;
              case "2":
                this.issueData.rytj = "";
                break;
              case "3":
                this.issueData.rytj = "";
                break;
              default:
                this.issueData.rytj = "";
                break;
            }
            //
            switch (this.issueData.ylfkfs) {
              case "01":
                this.issueData.ylfkfs = "";
                break;
              case "02":
                this.issueData.ylfkfs = "";
                break;
              case "03":
                this.issueData.ylfkfs = "";
                break;
              case "04":
                this.issueData.ylfkfs = "";
                break;
              case "05":
                this.issueData.ylfkfs = "";
                break;
              case "06":
                this.issueData.ylfkfs = "";
                break;
              case "07":
                this.issueData.ylfkfs = "";
                break;
              case "08":
                this.issueData.ylfkfs = "";
                break;
              default:
                this.issueData.ylfkfs = "";
                break;
            }
            //
            switch (this.issueData.zydm) {
              case "11":
                this.issueData.zydm = "";
                break;
              case "13":
                this.issueData.zydm = "";
                break;
              case "17":
                this.issueData.zydm = "";
                break;
              case "21":
                this.issueData.zydm = "";
                break;
              case "24":
                this.issueData.zydm = "";
                break;
              case "27":
                this.issueData.zydm = "";
                break;
              case "31":
                this.issueData.zydm = "";
                break;
              case "37":
                this.issueData.zydm = "";
                break;
              case "51":
                this.issueData.zydm = "";
                break;
              case "54":
                this.issueData.zydm = "";
                break;
              case "70":
                this.issueData.zydm = "";
                break;
              case "80":
                this.issueData.zydm = "()";
                break;
              default:
                this.issueData.zydm = "";
                break;
            }
            //
            switch (this.issueData.hyzk) {
              case "10":
                this.issueData.hyzk = "";
                break;
              case "20":
                this.issueData.hyzk = "";
                break;
              case "30":
                this.issueData.hyzk = "";
                break;
              case "40":
                this.issueData.hyzk = "";
                break;
              case "90":
                this.issueData.hyzk = "";
                break;
            }
            //
             switch (this.issueData.rybq) {
              case "1":
                this.issueData.rybq = "";
                break;
              case "2":
                this.issueData.rybq = "";
                break;
              case "3":
                this.issueData.rybq = "";
                break;
              case "4":
                this.issueData.rybq = "";
                break;
             
            }
          } else {
            this.$message.error(res.data.msg);
          }
        })
        .catch(error => {
          //this.$message.error('');
        });
    },
    //
    printBtn() {
      let subOutputRankPrint = document.getElementById("print");
      let newContent = subOutputRankPrint.innerHTML;
      let oldContent = document.body.innerHTML;
      document.body.innerHTML = newContent;
      window.print();
      window.location.reload();
      document.body.innerHTML = oldContent;
      return false;
    },
    //
    timeFn() {
      //di
      //
      var dateEnd = new Date(this.medicalText.outDate.replace(/-/g, "/")); //-/new Date
      var dateBegin = new Date(this.medicalText.inDate.replace(/-/g, "/")); //
      console.log(dateBegin);
      console.log(dateEnd);
      var dateDiff = dateEnd.getTime() - dateBegin.getTime(); //
      var dayDiff = Math.floor(dateDiff / (24 * 3600 * 1000)); //
      this.day = dayDiff;
      var leave1 = dateDiff % (24 * 3600 * 1000); //
      var hours = Math.floor(leave1 / (3600 * 1000)); //
      //
      var leave2 = leave1 % (3600 * 1000); //
      var minutes = Math.floor(leave2 / (60 * 1000)); //
      //
      var leave3 = leave2 % (60 * 1000); //
      var seconds = Math.round(leave3 / 1000);
      console.log(
        "  " +
          dayDiff +
          " " +
          hours +
          " " +
          minutes +
          " " +
          seconds +
          " "
      );
      console.log(
        dateDiff + "",
        dayDiff + "",
        leave1 + "",
        hours + "",
        minutes + "",
        seconds + ""
      );
    }
  },
  created() {
    this.getMedical();
    // this.timeFn();
  }
};
</script>



<style scoped>
h3 {
  /* margin-left: 44%;
  margin-bottom: 0; */
  margin: 0;
  padding: 0;
  text-align: center;
}
.ta td {
  width: 100px;
  height: 30px;
}
/*.ta tr:first-of-type td{
				border: 1px solid #fff;
			}*/
.biaotou {
  text-align: center;
}
.biaotou p {
  margin-left: 8%;
  display: inline-block;
}
.biaotou p:first-of-type {
  /* margin-left: 16%; */
}
</style>



